import React, { Component } from 'react';
import Helmet from 'react-helmet';
import PropTypes from 'prop-types';

import { withStyles } from 'material-ui/styles';
import { Typography, Button } from 'material-ui';
import TextField from 'material-ui/TextField';

import DialogComp from '../../components/dompetku/Dialog';
import SnackbarComp from '../../components/dompetku/Snackbar';

const styleSheet = theme => ({
  root: {
    backgroundImage: 'linear-gradient(to top, #48c6ef 0%, #6f86d6 100%)',
    height: '100vh',
  },
  card: {
    background: '#FFF',
    maxWidth: 500,
    borderRadius: 7,
    padding: 20,
    textAlign: 'center',
    position: 'absolute',
    left: '50%',
    top: '50%',
    WebkitTransform: 'translate(-50%, -50%)',
    transForm: 'translate(-50%, -50%)',
    [theme.breakpoints.down('sm')]: {
      width: '100%',
    },
  },
  gunakanVoucher: {
    margin: '40px 20px -10px',
  },
});

class Index extends Component {

  state = {
    dialogOpen: false,
    snackBarOpen: false,
    snackBarMsg: false,
    saldoVoucher: 10,
    hariSekarang: '',
    inputPassword: '',
    dompet: {},
  }

  componentWillMount = () => {
    this.getHariSekarang();
    if (this.props.dataDompet) {
      this.setState({ dompet: this.props.dataDompet });
    }
  }

  componentDidMount() {
    this.handleCekTanggal();
  }

  componentWillReceiveProps = nextProps => {
    if (nextProps.dataDompet && nextProps.dataDompet.saldoVoucher !== this.state.dompet.saldoVoucher) {
      this.setState({ dompet: nextProps.dataDompet });
    }
  }

  getHariSekarang = () => {
    const now = new Date();
    const start = new Date(now.getFullYear(), 0, 0);
    const diff = (now - start) + ((start.getTimezoneOffset() - now.getTimezoneOffset()) * 60 * 1000);
    const oneDay = 1000 * 60 * 60 * 24;
    const day = Math.floor(diff / oneDay);
    this.setState({ hariSekarang: day });
  }

  handleChange = name => event => {
    this.setState({ [name]: event.target.value });
  };
  handleSnackBar = on => {
    this.setState({ snackBarOpen: on });
  }
  handleSnackBarClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    this.setState({ snackBarOpen: false });
  };
  handleDialog = type => {
    this.setState({ dialogOpen: type }, this.handleCekTanggal());
  }
  handleSubmit = () => {
    const data = this.props.dataDompet;
    if (this.state.inputPassword === 'fajar') {
      this.props.dispatch(updateDompetku(
        data.tanggalUpdate,
        data.saldoVoucher - 1,
      ));
      this.setState({ snackBarMsg: true, snackBarOpen: true, dialogOpen: false }, () => this.props.dispatch(readDompetku()));
    } else {
      this.setState({ snackBarMsg: false, snackBarOpen: true });
    }
  }

  handleCekTanggal = () => {
    if (this.state.tanggalSekarang < this.props.dataDompet.tanggalUpdate) {
      console.log('jylo sukses');
    } else {
      console.log('jylo error');
    }
  }

  render() {
    const { classes } = this.props;
    const {
      hariSekarang,
      dialogOpen,
      snackBarOpen,
      inputPassword,
      snackBarMsg,
      dompet,
    } = this.state;
    return (
      <div>
        <Helmet defaultTitle="adnanfajlur">
          <title>adnanfajlur</title>
          <html lang="en" />
        </Helmet>
        <DialogComp open={dialogOpen} handleClose={() => this.handleDialog(false)} handleSubmit={this.handleSubmit} >
          <TextField
            id="inputPassword"
            label="Input Password"
            value={inputPassword}
            onChange={this.handleChange('inputPassword')}
            margin="normal"
          />
        </DialogComp>
        <SnackbarComp
          open={snackBarOpen}
          handleClose={this.handleSnackBarClose}
          snackBarMsg={snackBarMsg}
        >
        </SnackbarComp>
        <div className={classes.root}>
          <div className={classes.card}>
            <Typography>Hari Sekarang: {hariSekarang}</Typography>
            <Typography>Hari Terkahir Update: {dompet.tanggalUpdate}</Typography>
            <Typography>Saldo Voucher : {dompet.saldoVoucher}</Typography>
            <Button className={classes.gunakanVoucher} color="primary" variant="raised" onClick={() => this.handleDialog(true)}>Gunakan Voucher</Button>
          </div>
        </div>
      </div>
    );
  }
}
Index.propTypes = {
  classes: PropTypes.object,
  dataDompet: PropTypes.object,
  dispatch: PropTypes.func,
};

export default withStyles(styleSheet)(Index);



